@tailwind base;
@tailwind components;
@tailwind utilities;

/* 웹 폰트 불러오기 */
@font-face {
  font-family: "Pretendard-Regular";
  src: url("https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff")
    format("woff");
  font-weight: 400;
  font-style: normal;
}

html > body,
html > body input,
html > body select,
html > body textarea,
html > body pre,
html > body button {
  font-family: "Pretendard-Regular";
  text-underline-position: under;
  line-height: 1.3;
}

/* 커스텀 */
html,
body,
#root {
  height: 100%;
  /* background-color: #7075ff52; */
}

#root {
  display: flex;
  flex-direction: column;
}

.list_bg_go {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.list_bg_go > img {
  opacity: 0.7;
  width: 100%;
  top: -50px;
  position: absolute;
  z-index: -99;
}

.write_bg_go {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.write_bg_go > img {
  opacity: 0.7;
  width: 100%;
  top: -50px;
  left: 0;
  position: absolute;
  z-index: -99;
}

.op_bg_go {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.op_bg_go > img {
  opacity: 0.7;
  top: -90px;
  left: 0;
  position: absolute;
  z-index: -99;
}

.op_bg_go > span > img {
  opacity: 0.7;
  top: -70px;
  left: auto;
  right: 0;
  position: absolute;
  z-index: -99;
}

/* 상단 탭 1 */
.css-1ulbmay-MuiButtonBase-root-MuiTab-root.Mui-selected {
  background-color: rgba(28, 185, 238, 0.3);
}

.css-1ulbmay-MuiButtonBase-root-MuiTab-root.Mui-selected .first_tap {
  color: #fff !important;
}

.first_tap .first_tap_txt {
  margin-top: -3px;
}

/* 상단 탭 2 */
.css-71g5ry-MuiButtonBase-root-MuiTab-root.Mui-selected .sc_txt {
  color: #fff !important;
}
